    flex-direction: column;
    color: white;
    justify-content: center;
    margin: 200px 0;
    align-items: center;
}

.experienceContainer >h1{
    padding-bottom: 20px;
}

.experienceContainer__info{
    background: url("../../asset//we.webp");
